"use client";

import React, { useEffect, useMemo, useState } from "react";
import MemberModal, {
  MemberFormValue,
  MemberStatus,
} from "@/components/box/MemberModal";
import { Modal } from "@/components/ui/modal";

export type Member = {
  id: string;
  name: string;
  email?: string;
  phone?: string;
  status: MemberStatus;
  membershipExpiresAt?: string;
  notes?: string;
};

type Props = {
  initialMembers?: Member[];
};

const badgeClass = (status: MemberStatus) => {
  switch (status) {
    case "active":
      return "inline-flex items-center rounded-full bg-success-50 px-2.5 py-0.5 text-xs font-medium text-success-700 dark:bg-success-500/10 dark:text-success-400";
    case "expired":
      return "inline-flex items-center rounded-full bg-warning-50 px-2.5 py-0.5 text-xs font-medium text-warning-700 dark:bg-warning-500/10 dark:text-warning-400";
    default:
      return "inline-flex items-center rounded-full bg-gray-100 px-2.5 py-0.5 text-xs font-medium text-gray-700 dark:bg-white/[0.06] dark:text-gray-300";
  }
};

export default function MembersTable({ initialMembers }: Props) {
  const [members, setMembers] = useState<Member[]>(
    initialMembers ?? [
      {
        id: "m_1",
        name: "Juan Pérez",
        email: "juan@example.com",
        phone: "+34 600 111 222",
        status: "active",
        membershipExpiresAt: "2026-02-01",
        notes: "Hombro derecho",
      },
      {
        id: "m_2",
        name: "María López",
        email: "maria@example.com",
        phone: "+34 600 333 444",
        status: "expired",
        membershipExpiresAt: "2025-12-15",
      },
    ]
  );

  const [query, setQuery] = useState("");

  const [modalOpen, setModalOpen] = useState(false);
  const [modalMode, setModalMode] = useState<"create" | "edit">("create");
  const [editingMember, setEditingMember] = useState<Member | null>(null);

  const [deleteTarget, setDeleteTarget] = useState<Member | null>(null);

  const filtered = useMemo(() => {
    const q = query.toLowerCase().trim();
    if (!q) return members;
    return members.filter((m) =>
      `${m.name} ${m.email ?? ""} ${m.phone ?? ""}`.toLowerCase().includes(q)
    );
  }, [members, query]);

  const openCreate = () => {
    setModalMode("create");
    setEditingMember(null);
    setModalOpen(true);
  };

  const openEdit = (m: Member) => {
    setModalMode("edit");
    setEditingMember(m);
    setModalOpen(true);
  };

  const openDelete = (m: Member) => setDeleteTarget(m);

  const toggleActive = (id: string) => {
    setMembers((prev) =>
      prev.map((m) =>
        m.id === id
          ? { ...m, status: m.status === "inactive" ? "active" : "inactive" }
          : m
      )
    );
  };

  const confirmDelete = () => {
    if (!deleteTarget) return;
    setMembers((prev) => prev.filter((m) => m.id !== deleteTarget.id));
    setDeleteTarget(null);
  };

  const onSubmitModal = (val: MemberFormValue) => {
    if (modalMode === "create") {
      setMembers((prev) => [
        {
          id: `m_${Date.now()}`,
          ...val,
        },
        ...prev,
      ]);
    } else if (editingMember) {
      setMembers((prev) =>
        prev.map((m) =>
          m.id === editingMember.id ? { ...m, ...val } : m
        )
      );
    }
  };

  return (
    <>
      {/* Create / Edit modal */}
      <MemberModal
        isOpen={modalOpen}
        onClose={() => setModalOpen(false)}
        mode={modalMode}
        initialValue={editingMember ?? null}
        onSubmit={onSubmitModal}
      />

      {/* Delete confirmation */}
      <Modal
        isOpen={!!deleteTarget}
        onClose={() => setDeleteTarget(null)}
        className="max-w-md p-6"
      >
        <h4 className="mb-3 text-lg font-semibold text-gray-800 dark:text-white/90">
          Eliminar miembro
        </h4>
        <p className="mb-6 text-sm text-gray-500 dark:text-gray-400">
          ¿Seguro que querés eliminar a{" "}
          <strong>{deleteTarget?.name}</strong>? Esta acción no se puede deshacer.
        </p>
        <div className="flex justify-end gap-3">
          <button
            onClick={() => setDeleteTarget(null)}
            className="rounded-lg border border-gray-200 px-4 py-2 text-sm dark:border-gray-700"
          >
            Cancelar
          </button>
          <button
            onClick={confirmDelete}
            className="rounded-lg bg-error-500 px-4 py-2 text-sm text-white hover:bg-error-600"
          >
            Eliminar
          </button>
        </div>
      </Modal>

      {/* Top bar */}
      <div className="mb-4 flex items-center justify-between gap-3">
        <input
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder="Buscar miembro…"
          className="h-11 w-full max-w-sm rounded-lg border border-gray-200 bg-transparent px-4 text-sm dark:border-gray-800 dark:bg-white/[0.03]"
        />
        <button
          onClick={openCreate}
          className="rounded-lg bg-brand-500 px-4 py-2.5 text-sm font-medium text-white hover:bg-brand-600"
        >
          Añadir miembro
        </button>
      </div>

      {/* Table */}
      <div className="overflow-hidden rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
        <table className="w-full table-auto">
          <thead>
            <tr className="border-b border-gray-200 dark:border-gray-800">
              <th className="px-6 py-3 text-left text-xs text-gray-500">Miembro</th>
              <th className="px-6 py-3 text-left text-xs text-gray-500">Estado</th>
              <th className="px-6 py-3 text-left text-xs text-gray-500">Vence</th>
              <th className="px-6 py-3 text-left text-xs text-gray-500">
                Contacto
              </th>
              <th className="px-6 py-3 text-center text-xs text-gray-500">
                Acción
              </th>
            </tr>
          </thead>

          <tbody>
            {filtered.map((m) => (
              <tr
                key={m.id}
                className="border-t border-gray-100 dark:border-gray-800"
              >
                <td className="px-6 py-4 font-medium">{m.name}</td>
                <td className="px-6 py-4">
                  <span className={badgeClass(m.status)}>{m.status}</span>
                </td>
                <td className="px-6 py-4 text-sm">
                  {m.membershipExpiresAt ?? "—"}
                </td>
                <td className="px-6 py-4 text-sm">
                  {m.email ?? "—"}
                </td>

                {/* Actions (template pattern) */}
                <td className="px-6 py-[18px] text-center">
  <div className="flex items-center justify-center gap-2">
    {/* Eliminar */}
    <button
      type="button"
      onClick={() => openDelete(m)}
      title="Eliminar"
      className="text-gray-500 hover:text-error-500 dark:text-gray-400 dark:hover:text-error-500"
    >
      <svg
        className="fill-current"
        width="21"
        height="20"
        viewBox="0 0 21 20"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          fillRule="evenodd"
          clipRule="evenodd"
          d="M7.4163 3.79199C7.4163 2.54935 8.42366 1.54199 9.6663 1.54199H12.083C13.3256 1.54199 14.333 2.54935 14.333 3.79199V4.04199H16.5H17.5409C17.9551 4.04199 18.2909 4.37778 18.2909 4.79199C18.2909 5.20621 17.9551 5.54199 17.5409 5.54199H17.25V8.24687V13.2469V16.2087C17.25 17.4513 16.2427 18.4587 15 18.4587H6.75004C5.5074 18.4587 4.50004 17.4513 4.50004 16.2087V13.2469V8.24687V5.54199H4.20837C3.79416 5.54199 3.45837 5.20621 3.45837 4.79199C3.45837 4.37778 3.79416 4.04199 4.20837 4.04199H5.25004H7.4163V3.79199ZM15.75 13.2469V8.24687V5.54199H14.333H13.583H8.1663H7.4163H6.00004V8.24687V13.2469V16.2087C6.00004 16.6229 6.33583 16.9587 6.75004 16.9587H15C15.4143 16.9587 15.75 16.6229 15.75 16.2087V13.2469ZM8.9163 4.04199H12.833V3.79199C12.833 3.37778 12.4972 3.04199 12.083 3.04199H9.6663C9.25209 3.04199 8.9163 3.37778 8.9163 3.79199V4.04199ZM9.20837 8.00033C9.62259 8.00033 9.95837 8.33611 9.95837 8.75033V13.7503C9.95837 14.1645 9.62259 14.5003 9.20837 14.5003C8.79416 14.5003 8.45837 14.1645 8.45837 13.7503V8.75033C8.45837 8.33611 8.79416 8.00033 9.20837 8.00033ZM13.2917 8.75033C13.2917 8.33611 12.9559 8.00033 12.5417 8.00033C12.1275 8.00033 11.7917 8.33611 11.7917 8.75033V13.7503C11.7917 14.1645 12.1275 14.5003 12.5417 14.5003C12.9559 14.5003 13.2917 14.1645 13.2917 13.7503V8.75033Z"
          fill=""
        />
      </svg>
    </button>

    {/* Ver/Editar */}
    <button
      type="button"
      onClick={() => openEdit(m)}
      title="Editar"
      className="text-gray-500 hover:text-gray-800 dark:text-gray-400 dark:hover:text-white/90"
    >
      <svg
        className="fill-current"
        width="21"
        height="20"
        viewBox="0 0 21 20"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          fillRule="evenodd"
          clipRule="evenodd"
          d="M10.8749 13.8619C8.10837 13.8619 5.74279 12.1372 4.79804 9.70241C5.74279 7.26761 8.10837 5.54297 10.8749 5.54297C13.6415 5.54297 16.0071 7.26762 16.9518 9.70243C16.0071 12.1372 13.6415 13.8619 10.8749 13.8619ZM10.8749 4.04297C7.35666 4.04297 4.36964 6.30917 3.29025 9.4593C3.23626 9.61687 3.23626 9.78794 3.29025 9.94552C4.36964 13.0957 7.35666 15.3619 10.8749 15.3619C14.3932 15.3619 17.3802 13.0957 18.4596 9.94555C18.5136 9.78797 18.5136 9.6169 18.4596 9.45932C17.3802 6.30919 14.3932 4.04297 10.8749 4.04297ZM10.8663 7.84413C9.84002 7.84413 9.00808 8.67606 9.00808 9.70231C9.00808 10.7286 9.84002 11.5605 10.8663 11.5605H10.8811C11.9074 11.5605 12.7393 10.7286 12.7393 9.70231C12.7393 8.67606 11.9074 7.84413 10.8811 7.84413H10.8663Z"
          fill=""
        />
      </svg>
    </button>
  </div>
</td>

              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </>
  );
}
